h1{text-align:center;margin:0;padding-top:25px;text-transform:uppercase}

.hangman{position:relative;width:27vh;min-width:270px;height:231px;margin:auto}.hangman .stand .stand1,.hangman .stand .stand2,.hangman .stand .stand3{border:4px solid black;position:absolute}.hangman .stand .stand1{width:260px;bottom:0;left:50%;-webkit-transform:translate(-50%);transform:translate(-50%)}.hangman .stand .stand2{height:296.9px;top:-66px;right:0}.hangman .stand .stand3{width:137px;right:0;top:-66px}.hangman .stand .stand3::before,.hangman .stand .stand3::after{content:'';background-color:#000;display:inline-block;position:absolute}.hangman .stand .stand3::before{height:87px;-webkit-transform:rotate(-45deg);transform:rotate(-45deg);width:8px;top:-13px;right:26px}.hangman .stand .stand3::after{height:68px;left:-6px;top:-4px;width:8px}.hangman .person{margin-top:100px}.hangman .person .head{border:2px solid black;border-radius:50%;width:50px;height:50px;margin:auto}.hangman .person .body{margin:auto;border:1px solid black;height:126px;width:1px;background-color:black}.hangman .person .body.one-handed::before,.hangman .person .body.one-handed::after,.hangman .person .body.two-handed::before,.hangman .person .body.two-handed::after{background-color:#000;content:'';display:none;height:50px;position:relative;width:2px}.hangman .person .body.one-handed::before{right:18px;top:15px;-webkit-transform:rotate(45deg);transform:rotate(45deg);display:inline-block}.hangman .person .body.two-handed::after{left:18px;top:-40px;-webkit-transform:rotate(-45deg);transform:rotate(-45deg);display:inline-block}.hangman .person .legs.one-legged::before,.hangman .person .legs.one-legged::after,.hangman .person .legs.two-legged::before,.hangman .person .legs.two-legged::after{background-color:#000;content:'';display:none;height:50px;position:relative;width:2px}.hangman .person .legs.one-legged::before{right:18px;top:-8px;-webkit-transform:rotate(45deg);transform:rotate(45deg);display:inline-block}.hangman .person .legs.two-legged::after{left:18px;top:-8px;-webkit-transform:rotate(-45deg);transform:rotate(-45deg);display:inline-block}

.letters{max-width:500px;margin:10px auto}.letters button{outline:none;width:40px;height:40px;margin:5px;box-shadow:inset 0px 39px 0px -24px #e67a73;background-color:#e4685d;border-radius:4px;border:1px solid #ffffff;display:inline-block;cursor:pointer;color:#ffffff;font-family:Arial;font-size:15px;padding:6px 15px;text-decoration:none;text-shadow:0px 1px 0px #b23e35}.letters button:hover{background-color:#eb675e}.letters button:active{position:relative;top:2px}.letters button:disabled{background:gray;box-shadow:none}

.word input{background:none;border:none;outline:none;border-bottom:2px solid white;width:8%;max-width:40px;margin:2px;text-align:center;font-size:20px}

.dialog{position:absolute;right:0;left:0;top:33%;background-color:violet;box-shadow:0px 0px 20px 0px;width:50%;min-width:330px;max-width:400px;height:30%;margin:auto}.dialog p{font-size:26px;line-height:150px;margin:0}.dialog button{width:120px;height:35px;border:2px solid mediumorchid;background-color:transparent;outline:none;border-radius:2px;cursor:pointer;-webkit-transition:0.5s all;transition:0.5s all}.dialog button:hover{background-color:mediumorchid}

.app{text-align:center}

html,body{height:100%;margin:0}html{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-image:-webkit-gradient(linear, left bottom, left top, from(#a18cd1), to(#fbc2eb));background-image:linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);background-repeat:no-repeat}*{box-sizing:border-box}

